import React, { lazy, useState, useEffect } from 'react'
import moment from 'moment'
import { AnimationWrapper } from 'react-hover-animation'
import {ReactComponent as Satu} from './satu.svg'
import {ReactComponent as Dua} from './tiga.svg'
import {ReactComponent as Empat} from './lima.svg'
import {ReactComponent as Delapan} from './delapan.svg'
import {ReactComponent as Sembilan} from './sembilan.svg'
import MyLoader from './MyLoader.js'
import MyLoader2 from './MyLoader2.js'
import {
  CCard,
  CLink,
  CModal,
  CModalHeader,
  CModalBody,
  CCardBody,
  CImg,
  CCardGroup,
  CCol,
  CWidgetProgressIcon,
  CRow} from '@coreui/react'
import CIcon from '@coreui/icons-react'
import { fireAuth } from "../../fireApi";

function Loading2(props) {
  const [modal, setModal] = useState(false);
  if (!props.bingung) {
    return <MyLoader2/>
  }
  return(
    <>
    <AnimationWrapper className="col-3">
      <CWidgetProgressIcon
            header="Pengajuan"
            text="Anggaran Kegiatan"
            style={{backgroundColor:'#00923f'}}
            inverse
          >
            <Dua/>
            <CLink to='/pengajuan'class="stretched-link"></CLink>
      </CWidgetProgressIcon>      
    </AnimationWrapper>         
    <AnimationWrapper className="col-3">
      <CWidgetProgressIcon
          header="Pelaporan"
          text="Realisasi Kegiatan"
          style={{backgroundColor:'#00923f'}}
          inverse
        >
          <Satu/>
          <CLink to='/pelaporan'class="stretched-link"></CLink>
      </CWidgetProgressIcon>
</AnimationWrapper> 
<AnimationWrapper className="col-3">
<CWidgetProgressIcon
          header="Dashboard"
          text="Realisasi Anggaran"
          style={{backgroundColor:'#00923f'}}
          inverse
        >
          <Delapan/>
          <CLink to='/dboardrealisasi'class="stretched-link"></CLink>
      </CWidgetProgressIcon>
</AnimationWrapper> 
<CModal
        show={modal}
        onClose={()=> setModal(false)}
        size="md"
>
<CModalHeader closeButton><h5 class="Lato">Maaf, modul ini sedang dalam pengembangan</h5></CModalHeader>
<CModalBody>
<CCol sm="12" className="d-flex justify-content-center pb-3">
  <Sembilan/>
</CCol>
</CModalBody>
</CModal>
<AnimationWrapper className="col-3">
<CWidgetProgressIcon
          header="Upload"
          text="Bukti PJ"
          style={{backgroundColor:'#00923f'}}
          inverse
        >
          <Empat/>
          <CLink onClick = {() => setModal(true)} class="stretched-link"></CLink>
      </CWidgetProgressIcon>
</AnimationWrapper>
    </>            
  )
}

