const Dashboard = () => {
  const [time, setTime] = useState();
  const [hover, setHover] = useState(false)
  const [, setColor] = useState("")
  const [bingung, setBingung] = useState(false)
  useEffect(() => {
    const clock = moment().format('MMMM Do YYYY, h:mm:ss a')
    setTime(clock)
    setTimeout(() => {
      setBingung(true)
    }, 1500)
  });

  return (
    <>
    <CRow>
    <CCardGroup className="col-12 pb-3">      
      <CCard>
        <CCardBody style={{backgroundColor:"#d3dedd"}}>
          <Loading bingung={bingung} time={time} />
        </CCardBody>
      </CCard>
    </CCardGroup>
    </CRow>
    <CRow className="pb-3">
      <Loading2 bingung={bingung} />
    </CRow>
    </>
  )
}

export default Dashboard
